import React from "react";
import Layout from "./../components/Layout/Layout";
import { useParams, useNavigate } from "react-router-dom";
import { useSearch } from "../context/search";
const Search = () => {
  const [values, setValues] = useSearch();
  const navigate = useNavigate();
  return (
    <Layout title={"Search Results - RS Essentials"}>
      <div className="container-fluid category">
        <div className="pt-3 pb-5">
          <h1 className="text-center heads mb-1">Search Results</h1>
          <h6 className="text-center">
            {values?.results.length < 1
              ? "No Products Found"
              : `Found ${values?.results.length}`}
          </h6>
          <div className="d-flex flex-wrap mt-4">
            {values?.results.map((p) => (
              <div className="card m-2 pcard" key={p._id}>
                <img
                  src={`/api/v1/product/product-photo/${p._id}`}
                  className="card-img-top"
                  alt={p.name}
                />
                <div className="card-body">
                  <div className="card-name-price">
                    <h5 className="card-title ct">{p.name}</h5>
                    <h6 className="card-title card-price">
                      {p.price.toLocaleString("en-IN", {
                        style: "currency",
                        currency: "INR",
                      })}
                    </h6>
                  </div>
                  <p className="card-text cardp m-0">
                    {p.description.substring(0, 60)}...
                  </p>
                  <div className="card-name-price justify-content-center">
                    <button
                      className="btn btp"
                      onClick={() => navigate(`/product/${p.slug}`)}
                    >
                      More Details
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Search;
