import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import useCategory from "../hooks/useCategory";
import Layout from "../components/Layout/Layout";
const Categories = () => {
  const categories = useCategory();
  return (
    <Layout title={"All Categories - RS Essentials"}>
      <div className="container-fluid category pt-4 pb-5">
        <div className="row container-fluid m-0">
          {categories.map((c) => (
            <div
              className="col-lg-3 col-md-4 col-sm-6 col-12 mt-3 mb-2 gx-3 gy-3 d-flex justify-content-center"
              key={c._id}
            >
              <div className="card">
                <Link to={`/category/${c.slug}`} className="btn cat-btn">
                  {c.name}
                </Link>
              </div>
            </div>
          ))}
        </div>
      </div>
    </Layout>
  );
};

export default Categories;
