import React, { useState, useEffect } from "react";
import UserMenu from "../../components/Layout/UserMenu";
import Layout from "./../../components/Layout/Layout";
import { useAuth } from "../../context/auth";
import toast from "react-hot-toast";
import axios from "axios";
const Profile = () => {
  //context
  const [auth, setAuth] = useAuth();
  //state
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [phone, setPhone] = useState("");

  //get user data
  useEffect(() => {
    const { email, name, phone } = auth?.user;
    setName(name);
    setPhone(phone);
    setEmail(email);
  }, [auth?.user]);

  // form function
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.put("/api/v1/auth/profile", {
        name,
        email,
        password,
        phone,
      });
      if (data?.error) {
        toast.error(data?.error);
      } else {
        setAuth({ ...auth, user: data?.updatedUser });
        let ls = localStorage.getItem("auth");
        ls = JSON.parse(ls);
        ls.user = data.updatedUser;
        localStorage.setItem("auth", JSON.stringify(ls));
        toast.success("Profile Updated Successfully!");
      }
    } catch (error) {
      console.log(error);
      toast.error("Something Went Wrong!");
    }
  };
  return (
    <Layout title={"Your Profile"}>
      <div className="container-fluid m-0 p-3 dashboard">
        <div className="row">
          <div className="col-lg-3 col-md-4 col-12 mb-4 adf">
            <UserMenu />
          </div>
          <div className="col-lg-9 col-md-8 co-12 uppdb">
            <div className="form-container bgp">
              <div className="wrapper text-dark">
                <form onSubmit={handleSubmit}>
                  <h1 className="title">Edit Profile</h1>
                  <div className="input-box">
                    <input
                      className="inp1"
                      type="text"
                      value={name}
                      onChange={(e) => setName(e.target.value)}
                      placeholder="Enter Your Name"
                      autoFocus
                    />
                    <div className="icons pe-3">
                      <i class="fa-solid fa-user"></i>
                    </div>
                  </div>
                  <div className="input-box">
                    <input
                      className="inp1"
                      type="email"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      placeholder="Enter Your Email"
                      disabled
                    />
                    <div className="icons pe-3">
                      <i class="fa-solid fa-envelope"></i>
                    </div>
                  </div>
                  <div className="input-box">
                    <input
                      className="inp1"
                      type="password"
                      value={password}
                      onChange={(e) => setPassword(e.target.value)}
                      placeholder="Enter New Password"
                    />
                    <div className="icons pe-3">
                      <i class="fa-solid fa-lock"></i>
                    </div>
                  </div>
                  <div className="input-box">
                    <input
                      className="inp1"
                      type="text"
                      value={phone}
                      onChange={(e) => setPhone(e.target.value)}
                      placeholder="Enter Your Phone No."
                    />
                    <div className="icons pe-3">
                      <i class="fa-solid fa-phone"></i>
                    </div>
                  </div>
                  <button type="submit" className="btn1 cntbtn">
                    Update
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Profile;
