import React, { useState, useEffect } from "react";
import Layout from "./../components/Layout/Layout";
import axios from "axios";
import { useParams, useNavigate } from "react-router-dom";
import { NavLink, Link } from "react-router-dom";
import "../styles/ProductDetailsStyles.css";

const ProductDetails = () => {
  const params = useParams();
  const navigate = useNavigate();
  const [product, setProduct] = useState({});
  const [relatedProducts, setRelatedProducts] = useState([]);

  //initalp details
  useEffect(() => {
    if (params?.slug) getProduct();
  }, [params?.slug]);
  //getProduct
  const getProduct = async () => {
    try {
      const { data } = await axios.get(
        `/api/v1/product/get-product/${params.slug}`
      );
      setProduct(data?.product);
      getSimilarProduct(data?.product._id, data?.product.category._id);
    } catch (error) {
      console.log(error);
    }
  };
  //get similar product
  const getSimilarProduct = async (pid, cid) => {
    try {
      const { data } = await axios.get(
        `/api/v1/product/related-product/${pid}/${cid}`
      );
      setRelatedProducts(data?.products);
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <Layout title={"Product Details - RS Essentials"}>
      <div className="container-fluid p-0 pb-5 category">
        <div className="row pdc container-fluid ps-3 pe-5 product-details m-0 justify-content-center pt-3">
          <div className="col-lg-6 col-12 pdimg text-center pt-3">
            <img
              src={`/api/v1/product/product-photo/${product._id}`}
              className="w-50 pdIMG card-img-top"
              alt={product.name}
              height="300"
              width={"350px"}
            />
          </div>
          <div className="col-lg-6 col-12 product-details-info text-center">
            <h1 className="heads pdh text-center">Product Details</h1>
            <hr />
            <h6>Name : {product.name}</h6>
            <h6>Description : {product.description}</h6>
            <h6>
              Price :
              {product?.price?.toLocaleString("en-IN", {
                style: "currency",
                currency: "INR",
              })}
            </h6>
            <h6>Category : {product?.category?.name}</h6>
            <button onClick={() => navigate(`/`)} class="btn bt ms-1">
              All Products
            </button>
          </div>
        </div>
        <hr />
        <div className="row mt-4 m-0 container-fluid similar-products">
          <h4>Similar Products </h4>
          {relatedProducts.length < 1 && (
            <p className="text-center">No similar products found</p>
          )}
          <div className="d-flex cp flex-wrap">
            {relatedProducts?.map((p) => (
              <div className="card m-2 pcard" key={p._id}>
                <img
                  src={`/api/v1/product/product-photo/${p._id}`}
                  className="card-img-top"
                  alt={p.name}
                />
                <div className="card-body">
                  <div className="card-name-price">
                    <h5 className="card-title ct">{p.name}</h5>
                    <h5 className="card-title card-price">
                      {p.price.toLocaleString("en-IN", {
                        style: "currency",
                        currency: "INR",
                      })}
                    </h5>
                  </div>
                  <p className="card-text cardp">
                    {p.description.substring(0, 60)}...
                  </p>
                  <div className="card-name-price justify-content-center">
                    <button
                      className="btn btp"
                      onClick={() => navigate(`/product/${p.slug}`)}
                    >
                      More Details
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default ProductDetails;
